#connectParts {
    width: 40%;
    padding: 2%;
    box-sizing: border-box;
    background-color: black;
    border-radius: 5mm;
}

@media screen and (max-width: 700px) {
	#connectParts {
        width: 100%;
        padding: 4%;
        box-sizing: border-box;
        background-color: black;
        border-radius: 5mm;
    }
}

p {
    color: whitesmoke;
}

#button {
    margin-top: 15px;
    margin-bottom: 15px;
}

#alert {
    margin-top: 15px;
    margin-bottom: 15px;
}